	module.exports = {"data":{"post":{"html":"<p><a href=\"http://www.flickr.com/photos/papascott/251195634/\" title=\"Photo Sharing\"><img src=\"https://static.flickr.com/111/251195634_811a8ccbdb.jpg\" width=\"500\" height=\"375\" alt=\"24092006064\" /></a></p>\n<p>MamaMaus had to work today, but she took us along to Hamburg so we could go watch <a href=\"http://hamburg-freezers.de/\">ice hockey</a>. But the highlight for Christopher was the big playground (St. Petersburger Straße) at the <a href=\"http://www.plantenunblomen.hamburg.de/\">Planten un Blomen</a> park. It was new for us, too. They have a lot of water toys, (hand pumps, dams with trenches, even a water slide) and it was so warm today he was sorry we didn't bring his swim suit.</p>","fields":{"title":"Playground at Planten un Blomen","date":"24 Sep 2006","url":"https://papascott.de/2006-09-24-playground-at-planten-un-blomen/"}}},"pathContext":{"slug":"/2006-09-24-playground-at-planten-un-blomen/","prev":{"fields":{"title":"Trash your computer","date":"21 Sep 2006","slug":"/2006-09-21-trash-your-computer/"}},"next":{"fields":{"title":"OpenBC to become Xing","date":"25 Sep 2006","slug":"/2006-09-25-openbc-to-become-xing/"}}}}

